<template>
  <div class="home">
    <div @click="isShowForMask = true" class="link-minprogram">创建新的分享链接</div>
    <div v-if="shareInfo" class="container">
      <div class="name">{{shareInfo.name}}</div>
      <div class="type">{{shareInfo.type === 0 ? '公众号' : '小程序'}}</div>
      <div class="desc">{{shareInfo.introduce}}</div>
      <img v-if="shareInfo.code" class="code" :src="`${baseURL}${shareInfo.code}`" alt="">
      <div class="label">长按图片进入小程序</div>
    </div>
    <div v-else class="no">
      该分享链接已经失效
    </div>
    <div @click="isShowForMask = false" v-if="isShowForMask" class="mask">
      <img @click.stop="1" src="../assets/mini.jpg" alt="">
    </div>
  </div>
</template>

<script>
import axios from 'axios';
const baseURL = 'https://sign.ei-marketing.net/wechat-share'

export default {
  name: 'home',
  data () {
    return {
      shareId: '',
      shareInfo: {},
      baseURL: baseURL,
      isShowForMask: false
    }
  },
  created() {
    this.shareId = this.$route.query.shareId;
    axios.get(`${baseURL}/addOne.do?shareId=${this.shareId}`);
    this.getShareInfo(this.shareId)
  },
  methods: {
    getShareInfo(shareId) {
      axios({
        method: 'post',
        url: `${baseURL}/share/shareDetail.do`,
        params: {
          shareId
        }
      }).then((res) => {
        this.shareInfo = res.data.data
      })
    }
  }
}
</script>
